import SelectCheck from "./select_check"
import HelperMore from "./_helperMore"

const BaseControl = props => {
  const more = HelperMore.getMoreFromProps(props)
  let _optionPositive = HelperMore.getOptionPositive(more, {
    default: {
      Value: true,
      Text: "YES",
    },
  })
  let _optionNegative = HelperMore.getOptionNegative(more, {
    default: {
      Value: false,
      Text: "NO",
    },
  })
  let _options = [_optionPositive, _optionNegative]
  return SelectCheck({
    ...props,
    fnList: {
      ...props.fnList,
      getSourceData: () => {
        return _options
      },
    },
  })
}

export default BaseControl
